/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';

import MobileStepper from '@material-ui/core/MobileStepper';
import CircularProgress from '@material-ui/core/CircularProgress';

import './Signup.css';
import agent from '../../../agent';

class SignUp extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user: {
        name: '',
        number: '',
        college: 'NIIT',
        password: '',
        email: '',
      },
      activeStep: 0,
      loading: false,
      err: '',
    };
    this.colleges = [];
  }

  componentDidMount() {
    this.colleges = this.props.colleges.colleges;
    if (this.colleges.length !== 0) this.setState({ user: { ...this.state.user, college: this.colleges[0] } });
  }

  componentWillReceiveProps(np) {

  }

  handleNext = () => {
    this.setState({ activeStep: this.state.activeStep + 1 });
  };

  handleChangeCollege(e) {
    this.setState({ user: { ...this.state.user, college: e.target.value } });
  }

  moveNext(e) {
    e.preventDefault();
    this.setState({ err: '' });

    if (this.state.activeStep === 0) {
      const { name, number, college } = this.state.user;
      const data = {
        name,
        phone: number,
        college: college.id,
      };
      this.setState({ loading: true });
      agent.Auth.register(data).then((res) => {
        this.handleNext();
        this.setState({ loading: false, user: { ...this.state.user, secret: res.data.secret } });
      }).catch((err) => {
        if (err.response) {
          this.setState({ error: err.response.data.error, loading: false });
        }
      });
    } else {
      const { secret, email, password } = this.state.user;
      const data = {
        secret,
        email,
        password,
      };
      this.setState({ loading: true, error: '' });

      agent.Auth.updateUser(data).then((res) => {
        this.props.changeScreen({ username: email, password });
        agent.Auth.sendVerificationEmail({ email })
          .then((res) => console.log('verificaiton email res', res))
          .catch((err) => console.log('err in sending verification email', err.response));
        this.setState({ loading: false, secret: res.data.secret, error: '' });
      }).catch((err) => {
        if (err.response) { this.setState({ error: err.response.data.error, loading: false, error: '' }); }
      });
    }
  }

  render() {
    const { college } = this.state.user;
    return (
      <div className="">
        <div className=" ba tc w-100 pa4 box vertical-align " style={{ minHeight: '70vh', position: 'relative' }}>


          <h1 className=" heading center mt1 mb0 ">Instagram</h1>
          <MobileStepper
            variant="progress"
            steps={3}
            position="static"
            activeStep={this.state.activeStep}
            style={{ width: '100%' }}
            
          />
          {

              this.state.activeStep === 0
                ? (
                  <form className="tc" onSubmit={this.moveNext.bind(this)}>
                    <legend className="tc center subheading">Sign up To see photos and videos of your friends</legend>
                    <div className="input-label-div">
                      <input
                        type="text"
                        name="name"
                        className="inp"
                        value={this.state.user.name}
                        onChange={(e) => this.setState({ user: { ...this.state.user, name: e.target.value } })}
                        required
                      />
                      <label className="inp-label" name="name">
                        Name
                      </label>
                    </div>
                    <div className="input-label-div">
                      <input
                        type="text"
                        name="phone-number"
                        className="inp"
                        required
                        maxLength="10"
                        // minLength={10}
                        pattern="[1-9]{1}[0-9]{9}"
                        value={this.state.user.number}
                        onChange={(e) => this.setState({ user: { ...this.state.user, number: e.target.value } })}
                      />
                      <label className="inp-label">
                        Mobile number
                      </label>
                    </div>
                    <div className="input-label-div">
                      <select id="college" className=" inp mt1" onChange={(e) => this.handleChangeCollege(e)}>
                        {
                        this.colleges.map((c, i) => (
                          <option value={c} key={c.id}>{c.name}</option>
                        ))
                      }
                      </select>
                      <label className="inp-label">
                       Select College
                      </label>
                    </div>

                    <div className="flex    ">
                      <input type="submit" value="Next" className="color-btn" />

                    </div>
                    <p className="err-txt">{this.state.error}</p>
                  </form>
                )
                : (
                  <form className="tc" onSubmit={this.moveNext.bind(this)}>
                    <legend className="tc center subheading">Sign up To see photos and videos of your friends</legend>
                    <div className="input-label-div">
                      <input
                        type="email"
                        name="email"
                        className="inp"
                        value={this.state.user.email}
                        onChange={(e) => this.setState({ user: { ...this.state.user, email: e.target.value } })}
                        required
                      />
                      <label className="inp-label">
                      Email
                      </label>

                    </div>
                    {
                        college.has_college_email
                          ? (
                            <p className="small-txt">
                              {' '}
                              {'Enter you college specific email id to register.'}
                              {' '}
                            </p>
                          ) : null
                      }
                    <div className="input-label-div">
                      <input
                        type="password"
                        name="password"
                        className="inp"
                        required
                        value={this.state.user.password}
                        onChange={(e) => this.setState({ user: { ...this.state.user, password: e.target.value } })}
                      />
                      <label className="inp-label">
                      Password
                      </label>
                    </div>
                    <p className="err-txt">{this.state.error}</p>
                    <input type="submit" value="Sign Up" className="color-btn" />
                  </form>

                )

          }

          {
                this.state.loading
                  ? (
                    <div className="loading">
                      {' '}
                      <CircularProgress />
                    </div>
                  ) : null
            }

        </div>
        <div className=" tc box mt3" type="button">
          <button onClick={() => this.props.changeScreen()} className="btn">
            <p>
                Have an account?
              <a><span>Log in</span></a>
            </p>

          </button>
        </div>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(SignUp);
