import './App.css';
import 'react-toastify/dist/ReactToastify.css';

import { ConnectedRouter } from 'connected-react-router';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Route, Switch } from 'react-router';
import { push } from 'react-router-redux';
import { Slide, ToastContainer } from 'react-toastify';

import agent from './agent';
import { AuthScreen, HomePage, AdminPage,MyAccount } from './components';

import { APP_LOAD, REDIRECT, SET_USER } from './constants/actionTypes';
import { store } from './store';
import { WEB_TOKEN, USER_DETAILS } from './constants/otherConstants';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      openSideDrawer: false,
      user: {},
    };

    this.handleToggleBtn = this.handleToggleBtn.bind(this);
    this.handleBackdrop = this.handleBackdrop.bind(this);
  }

  componentWillMount() {
    const token = window.localStorage.getItem(WEB_TOKEN);
    let userDetails = window.localStorage.getItem(USER_DETAILS);
    // if (userDetails !== null) {
    //   userDetails = JSON.parse(userDetails);
    //   this.setState({ user: this.props.user });
    //   this.props.setUser(userDetails);
    // }
    if (token) {
      agent.setToken(token);
    }
    if (token === null) {
      // console.log('roken', token);
      store.dispatch(push('/'));
      this.props.onRedirect('/');
    }
    if (this.props.user) this.setState({ user: this.props.user });
    this.props.onLoad(token ? 1 : null, token);
  }

  componentWillReceiveProps(nextProps) {
    // console.log('App.js', nextProps);
    if (this.props.history.location !== nextProps.history.location) {
      this.props.onRedirect(nextProps.history.location);
    }
    if (nextProps.redirectTo && nextProps.redirectTo !== this.props.redirectTo) {
      store.dispatch(push(nextProps.redirectTo));
    }
    if (nextProps.user) this.setState({ user: nextProps.user });
  }

  handleToggleBtn=() => {
    this.setState({
      openSideDrawer: true,
    });
  }

  handleBackdrop=() => {
    this.setState({
      openSideDrawer: false,
    });
  }

  render() {
    let sideDrawer;
    let backdrop;

    // if (this.state.openSideDrawer) {
    //   sideDrawer = <SideDrawer />;
    //   backdrop = <Backdrop click={this.handleBackdrop} />;
    // }
    const role = window.localStorage.getItem('role');

    return (
      <div style={{ height: '100%' }}>
        <ToastContainer
          autoClose={2000}
          closeOnClick
          transition={Slide}
          pauseOnFocusLoss={false}
        />
        <ConnectedRouter history={this.props.history}>
          <Switch>
            {/* <Route path="/" component={AdminPage  } exact /> */}
            <Route path="/" component={AuthScreen} exact />
            <Route path="/user/:userName" component={HomePage} exact />
            <Route path="/admin" component={AdminPage} exact />
            <Route path="/account" component={MyAccount} exact />
          </Switch>
        </ConnectedRouter>

        {sideDrawer}
        {backdrop}

      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  appLoaded: state.common.appLoaded,
  appName: state.common.appName,
  currentUser: state.common.currentUser,
  redirectTo: state.common.redirectTo,
  apiKey: state.common.apiKey,
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
  onLoad: (payload, apiKey) => dispatch({
    type: APP_LOAD,
    payload,
    apiKey,
    skipTracking: true,

  }),
  onRedirect: (route) => dispatch({ type: REDIRECT, payload: route }),
  clearReducer: () => dispatch({ type: 'CLEAR' }),
  setUser: (user) => dispatch({ type: SET_USER, payload: user }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(App);
