import React, { Component } from 'react';
import { connect } from 'react-redux';
import Modal from '@material-ui/core/Modal';
import AddAPhotoOutlinedIcon from '@material-ui/icons/AddAPhotoOutlined';

import ReactCrop from 'react-image-crop';
import 'tachyons';
import './styles.css';


// import 'react-image-crop/lib/ReactCrop.scss';
import 'react-image-crop/dist/ReactCrop.css';
import { Icon } from '@material-ui/core';

class ProfilePIc extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tookInput: true,
      crop: {
        aspect: 1 / 1,
        width: 0,
      },
      cropped: false,
      img: '',
      imgBlob: {},
      imgproperties: {},
    };
  }

  componentWillMount() {
    const { photo, changProfile } = this.props;
    if (changProfile) {
      // console.log(photo);
      this.setState({
        tookInput: false, img: photo, changProfilePic: true, cropped: false,
      });
    } else {
      // console.log(photo);
      this.setState({ tookInput: true, img: photo, changProfilePic: false });
    }
  }

  componentWillReceiveProps(np) {
    const { photo, changProfile } = np;
    if (changProfile) {
      this.setState({
        tookInput: false, img: photo, changProfilePic: true, cropped: false,
      });
    } else {
      console.log(photo)
      this.setState({ tookInput: true, img: photo, changProfilePic: false });
    }
  }

  onImageLoaded = (image) => {
    this.imageRef = image;
  };

  getBase64(file, cb) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      cb(reader.result);
    };
    reader.onerror = function (error) {
      console.log('Error: ', error);
    };
  }

  getCroppedImg(image, crop, fileName) {
    const i = this.imageRef;
    // console.log(crop);
    if (crop.width === 0 || crop.height === 0) crop = { ...crop, width: i.width, height: i.height };

    const canvas = document.createElement('canvas');
    const scaleX = i.naturalWidth / i.width;
    const scaleY = i.naturalHeight / i.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext('2d');
    // console.log(scaleX,scaleY)
    ctx.drawImage(
      i,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height,
    );
    const {
      name, lastModified, lastModifiedDate, webkitRelativePath, type,
    } = this.state.imgproperties;
    const base64Image = canvas.toDataURL('image/png');

    canvas.toBlob((blob) => {
      blob.name = name;
      blob.lastModified = lastModified;
      blob.lastModifiedDate = lastModifiedDate;
      blob.webkitRelativePath = webkitRelativePath;
      this.setState({
        img: base64Image,
        imgBlob: blob,
        crop: {
          aspect: 1 / 1,
          width: 20,
        },
        cropped: true,
      });
    }, type, 1);
  }

  onChange = (crop) => {
    this.setState({ crop });
  };

  selectProfilePic(pic) {
    if (pic === null) {
      this.setState({ img: null, tookInput: true, cropped: true });
    } else {
      this.getBase64(pic[0], (res) => this.setState({ img: res, tookInput: true, cropped: false }));
      this.setState({ imgproperties: pic[0] });
    }
  }

  render() {
    return (
      <div>
        {
          !this.state.changProfilePic
            ? (
              <div className="profile-img-div mt4 mb2 center">
                <div className="center">

                  {
                    (this.state.img) ? 
                      
                      <img src={this.state.img} id="a" className="profile-img" alt="" /> :<img src="https://cdn-images-1.medium.com/max/1200/1*PkrwrLwaq68CaqLPn7jBIw.png" className="profile-img" alt='' /> 
                  }
                </div>
              </div>
            )
            : (
              <div>
                <div className="profile-img-div mt4 mb2 center">
                  <div className="center">
                    <input
                      type="file"
                      id="avatar"
                      name="avatar"
                      accept="image/png, image/jpeg"
                      className="img-input"
                      onChange={(e) => this.selectProfilePic(e.target.files)}
                    />
                    {
                      this.state.img ? <img src={this.state.img} id="a" className="profile-img" alt="" />
                        : <AddAPhotoOutlinedIcon className="profile-img" />
                    }
                  </div>
                </div>
                <div>

                  <Modal
                    open={this.state.tookInput}
                          // onClose={handleClose}
                    aria-labelledby="simple-modal-title"
                    className="tc center modal"

                    aria-describedby="simple-modal-description"
                  >
                    {
                    (this.state.cropped)
                      ? (
                        <div
                          className="tc"
                          style={{
                            alignContent: 'center', alignItems: 'center', display: 'flex', flexDirection: 'column', width: '80%', height: '80vh', padding: '40px', margin: 'auto', background: 'rgba(0,0,0,0.8)',
                          }}
                        >
                          <img src={this.state.img} />
                          <div className="center">
                            <input
                              type="file"
                              id="avatar"
                              name="avatar"
                              accept="image/png, image/jpeg"
                              className="img-input"
                              onChange={(e) => this.selectProfilePic(e.target.files)}
                            />
                            <p className="profile-btn white center mt0 pt0 mb0">Choose another</p>


                          </div>
                          <button type="button" onClick={() => this.props.selectProfilePic(this.state.imgBlob)} className="color-btn  center mt0 pt0 mb0"> Upload Picture</button>
                          <button type="button" onClick={() => this.setState({ tookInput: false, cropped: false })} className="profile-btn white center mt0 pt0 mb0">  Cancel</button>
                        </div>
                      )
                      : (
                        <div
                          className="tc"
                          style={{
                            alignContent: 'center', alignItems: 'center', display: 'flex', flexDirection: 'column', width: '80%', height: '80vh', padding: '40px', margin: 'auto', background: 'rgba(0,0,0,0.8)',
                          }}
                        >
                          <ReactCrop
                            src={this.state.img}
                            crop={this.state.crop}
                            onChange={this.onChange}
                            ruleOfThirds
                            onImageLoaded={this.onImageLoaded}
                          />
                          <button type="button" onClick={() => this.selectProfilePic(null)} className="profile-btn white center mt0 pt0 mb0"> Remove Picture</button>
                          <button type="button" onClick={() => this.getCroppedImg(this.state.img, this.state.crop, 'abc')} className="color-btn  center mt0 pt0 mb0"> Crop Picture</button>
                          <button type="button" onClick={() => this.setState({ tookInput: false, cropped: false })} className="profile-btn white center mt0 pt0 mb0">  Cancel</button>
                        </div>
                      )
                    }

                  </Modal>

                </div>
              </div>
            )
        }


      </div>
    );
  }
}


const mapStateToProps = (state) => ({
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ProfilePIc);
