import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import DeleteTwoToneIcon from '@material-ui/icons/DeleteTwoTone';
import './styles.css';
import Switch from '../../../common/Components/Switch';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      updated: false,
      loading: false,
      error: '',
      link: {},
    };
  }


  componentWillMount() {
    console.log(this.link);
    this.setState({ updated: false, link: this.props.link,showDelete:false });
    // console.log("snapshot",this.props.snapshot)
  }

  componentWillReceiveProps(np) {
    console.log(np);
    if (np.link !== this.state.link) this.setState({ render: !this.state.render, updated: false,showDelete:false });
  }

  handleTextChange(key, value) {
    this.setState({ error: '' });
    this.setState({ link: { ...this.state.link, [key]: value }, updated: true,showDelete:false });
  }

  createLink(e, link) {
    // this.setState({ error: '' });
    e.preventDefault();
    // console.log(link);
    if (link.url && link.title) {
      this.setState({showDelete:false})
      this.props.createLink(link);
    } else {
      this.setState({ error: 'Title or url cannot be empty!!!' });
    }
  }

  deleteLink(link) {
    this.props.deleteLink(link);
  }

  togelSwitch(val) {
    this.setState({ link: { ...this.state.link, visible: (val.target.checked) }, updated: true,showDelete:false });
  }

  render() {
    const { title, url, visible } = this.state.link;
    return (
      <div className=" link-div">
        <form onSubmit={(e) => this.createLink(e, this.state.link)}>
          <div className="flex justify-between">
            <div className="flex flex-column w-80">

              <input
                className="b"
                value={title}
                placeholder="Enter Link Title"
                onChange={(e) => this.handleTextChange('title', e.target.value)}
              />
              <input
                value={url}
                type='url'
                placeholder="https://example.com"
                onChange={(e) => this.handleTextChange('url', e.target.value)}
              />
            </div>
            <Switch togelSwitch={this.togelSwitch.bind(this)} val={visible} />
          </div>
          <p className="err-txt">{`${this.state.error}`}</p>
          <div className="flex justify-end">

            <div className="flex items-center justify-center">
              {
                this.state.updated ? (
                  <button className="color-btn" type="submit">
                Save
                  </button>
                ) : null
              }

              <DeleteTwoToneIcon className="pointer " onClick={() => this.setState({showDelete:true})} />
            </div>

          </div>
          {
            this.state.showDelete ? (
              <div className=" ">
                <p className='b' > Are you sure to delete link? </p>
                <div className="flex items-center mt2 justify-between">
                  <button className='color-btn w-50 tc' onClick={()=> this.setState({showDelete:false})} type="button" >Cancel</button>
                  <button className='color-btn w-50 tc ' onClick={()=>this.deleteLink(this.state.link)} type="button">Delete</button>
                </div>

              </div>
            ) : null
          }

        </form>
      </div>


    );
  }
}


const mapStateToProps = (state) => ({
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
