import {
    ASYNC_START,
    ASYNC_END,
    GET_LINKS
  } from '../../constants/actionTypes';
    
  export default (state = {}, action) => {
    switch (action.type) {
      case ASYNC_START:
    
        return { ...state };
    
    
      case ASYNC_END:
    
        return { ...state };
      case GET_LINKS:
        return {...state,links:action.payload}
    
      default:
        return state;
    }
    
    return state;
  };
    