import {
    ASYNC_START,
    ASYNC_END,
  } from '../../constants/actionTypes';
    
  export default (state = {}, action) => {
    switch (action.type) {
      case ASYNC_START:
    
        return { ...state };
    
    
      case ASYNC_END:
    
        return { ...state };
     
    
      default:
        return state;
    }
    
    return state;
  };
    