import axios from 'axios';

// import { RECORD_LIMIT } from './constants/pagination';

// const API_ROOT = process.env.NODE_ENV === 'production' ? 'https://www.extremist.team' : ' http://127.0.0.1:8000/'; // 104.215.141.154     127.0.0.1

const API_ROOT = process.env.NODE_ENV === 'production' ? 'https://www.extremist.team/event_manager/api/' : 'https://www.extremist.team/event_manager/api/'; 
axios.defaults.baseURL = API_ROOT;
axios.defaults.timeout = 4000;

const setToken = (token) => {
   console.log(token);
  axios.defaults.headers.common = { Token: token };
};
const responseBody = (response) => {
  //  console.debug('RESPONSE', response);
  return response;
};

const requests = {
  delete: (url,data) => axios.delete(`${url}`,{ data:data }).then(responseBody),
  get: (url) => axios.get(`${url}`).then(responseBody),
  // getAndPushToUrl: url => superagent.get(`${url}`).use(tokenPlugin).then(responseBody),
  getPaginated: (url, pageNum) => axios
    .get(`${url}`)
    .set('page_num', pageNum)
    .then(responseBody),
  put: (url, body) => axios.put(`${url}`, body).then(responseBody),
  post: (url, body) => axios.post(`${url}`, body).then(responseBody),
  postFile: (url, key, file) => {
    const formData = new FormData();
    formData.append(key, file);
    console.log("Form data",formData.get(key),formData.getAll(key)  );

    let c  = axios
      .post(`${url}`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      .then(responseBody);
    console.log(c)
    return c;
  },
};

const Auth = {
  login: (data) => requests.post('/login/', data),
  logout: () => requests.post('/logout/'),
  register: (data) => requests.post('/register/', data),
  updateUser:(data) => requests.post('/update/',data),
  getColleges:() => requests.get('/colleges/'),
  uploadProfileImage:(key,file) => requests.postFile('/upload/profile/',key,file),
  getUserDetails:(user) => requests.post('/user/',user),
  sendVerificationEmail :(email) => requests.post('/validate-email/',email)
};
const AdminPage ={
  createLink:(link) => requests.post('/links/',link),
  deleteLink :(link) => requests.delete('/links/',link),
  getLinks :() => requests.get('/links/')
}

export default {
  Auth,
  setToken,
  AdminPage,
  requests,

};
