import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';

import './AdminPage.css';
import { push } from 'react-router-redux';
import SideDrawer from './components/SideDrawer';
import Link from './components/Link';

import { store } from '../../store';
import agent from '../../agent';
import { GET_LINKS } from '../../constants/actionTypes';
import {BASE_URL} from '../../constants/otherConstants'
import ToolTip from '../../common/Components/ToolTip'
import { Toolbar } from '@material-ui/core';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
    };
    this.links = [];
    this.location=""
  }

  componentWillMount() {
    console.log((window.location))
    // this.location = window.location.href.split(/)
    // this.location = this.location
    if (this.props.user) this.setState({ user: this.props.user.user });
    this.props.getLinks();
  }

  componentWillReceiveProps(np) {
    
    if (this.links !== np.links.links) this.links = np.links.links;
  }

  addLink() {
    const link = {
      title: null,
      url: null,
      visible: true,
    };
    this.links = [...this.links, link];
    this.setState({ render: !this.state.render });
  }

  createLink(link) {
    // this.links[link.index] = link;
    const data = {
      url: link.url,
      visible:link.visible,
      title: link.title,
    };
    if(link.id)
        data.id = link.id
    
    agent.AdminPage.createLink(data).then((res) => {
    
      this.props.getLinks();
    }).catch((err) => {
    
      if (err.response) console.log(err.response);
    });
    this.setState({ render: !this.state.render });
  }

  deleteLink(link) {
    
    if (link.id) {
      const data = {

        id: link.id,
      };
      
      agent.AdminPage.deleteLink(data).then((res) => {
      
        this.props.getLinks();
      }).catch((err) => {
      
        if (err.response) console.log(err.response);
      });
    }else{
        this.links = this.links.filter((l, i) => i !== link.index);
    }
    this.setState({ render: !this.state.render });

  }

  renderLinks() {
    return this.links.map((l, i) => (
      <Link
        link={{ ...l, index: i }}
        key={Math.random() * 100}
        deleteLink={this.deleteLink.bind(this)}
        createLink={this.createLink.bind(this)}
      />
    ));
  }

  render() {
    return (
      <div className="admin-bg nav-margin">
        <SideDrawer user={this.state.user} />

        <article className=" dyna-width center">
        <ToolTip innertext={'Yout Sharable Link'} >
          <div className=" dyna-width center flex tc">
            
            <p className="  center ba pa1 w-100 block flex-wrap " style={{overflowY:'auto'}}>
              {'Use This url to share your profile '}
              <a className='blue pointer grow' target="_blank"  href={`${window.location.origin}${BASE_URL}/user/${this.state.user.username}`}>{`${window.location.origin}${BASE_URL}/user/${this.state.user.username}`}</a>
            </p>
          </div>
          </ToolTip>

          <div className=" center">
            <div className=" color-btn dyna-width tc center" onClick={this.addLink.bind(this)} >Add new Link</div>
            {this.renderLinks()}
          </div>
        </article>


      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
});

const mapDispatchToProps = (dispatch) => ({
  getLinks: () => dispatch({ type: GET_LINKS, payload: agent.AdminPage.getLinks() }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
